import type { Employee } from "@/lib/mock-data"
import { Trophy, Medal, Award } from "lucide-react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { Separator } from "@/components/ui/separator"

interface LeaderboardTableProps {
  employees: Employee[]
  title: string
}

export function LeaderboardTable({ employees, title }: LeaderboardTableProps) {
  const getRankIcon = (rank: number) => {
    if (rank === 1) return <Trophy className="w-5 h-5 md:w-6 md:h-6 text-[#FFB100]" />
    if (rank === 2) return <Medal className="w-5 h-5 md:w-6 md:h-6 text-[#004EFF]" />
    if (rank === 3) return <Award className="w-5 h-5 md:w-6 md:h-6 text-black" />
    return null
  }

  const getRankColor = (rank: number) => {
    if (rank === 1) return "bg-[#FFB100]/5 border-l-4 border-[#FFB100]"
    if (rank === 2) return "bg-[#004EFF]/5 border-l-4 border-[#004EFF]"
    if (rank === 3) return "bg-gray-100 border-l-4 border-gray-900"
    return "hover:bg-gray-50 border-l-4 border-transparent"
  }

  return (
    <div className="h-full w-full flex flex-col p-4 md:p-6 lg:p-8 bg-gray-50 overflow-hidden font-sans">
      <div className="max-w-7xl mx-auto w-full px-2 md:px-4 flex flex-col h-full">
        <CardHeader className="text-center pb-6 md:pb-8 flex-shrink-0">
          <CardTitle className="text-3xl md:text-4xl lg:text-5xl font-bold text-gray-900 tracking-tight">{title}</CardTitle>
        </CardHeader>

        <Card className="overflow-hidden flex-1 flex flex-col min-h-0 shadow-xl border-0 rounded-2xl bg-white">
          <CardContent className="p-0 flex-1 flex flex-col overflow-hidden">
            <div className="flex-1 overflow-y-auto scrollbar-hide">
              {employees.map((employee, index) => (
                <div key={employee.id}>
                  <div
                    className={`flex items-center gap-3 md:gap-4 p-4 md:p-5 transition-all duration-300 animate-in fade-in slide-in-from-left ${getRankColor(index + 1)}`}
                    style={{ animationDelay: `${index * 50}ms` }}
                  >
                    <div className="flex items-center gap-3 min-w-[80px] md:min-w-[100px]">
                      <span className={`text-xl md:text-2xl font-bold w-8 text-right ${index < 3 ? 'text-gray-900' : 'text-gray-400'}`}>{index + 1}</span>
                      <div className="w-6 h-6 flex items-center justify-center">
                        {getRankIcon(index + 1)}
                      </div>
                    </div>

                    <Avatar className="w-10 h-10 md:w-12 md:h-12 bg-gray-100 shadow-sm flex-shrink-0 border border-gray-200">
                      <AvatarFallback className={`text-sm md:text-base font-bold ${index === 0 ? 'text-[#FFB100]' : index === 1 ? 'text-[#004EFF]' : 'text-gray-700'}`}>
                        {employee.avatar}
                      </AvatarFallback>
                    </Avatar>

                    <div className="flex-1 min-w-0">
                      <h3 className="text-base md:text-lg font-bold text-gray-900 leading-tight truncate">{employee.name}</h3>
                      <p className="text-xs md:text-sm text-gray-500 truncate">{employee.department}</p>
                    </div>

                    <div className="text-right flex-shrink-0 pr-4">
                      <div className="text-xl md:text-2xl font-bold text-gray-900">{employee.dvdScore.toLocaleString()}</div>
                      <p className="text-xs text-gray-500 uppercase tracking-wide font-medium">Points</p>
                    </div>
                  </div>
                  {index < employees.length - 1 && <Separator className="bg-gray-100" />}
                </div>
              ))}
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}